var CURNET = 'netidtst1'
var APIHOST = "http://127.0.0.1:4884"


var get_cur_net = function () {
    if (process.env.NODE_ENV != 'development') {
        return "mainnet01"

    }
    return CURNET
}

var get_api_host = function () {
    if (process.env.NODE_ENV != 'development') {
        return "https://api.chainweb.com/chainweb/0.0/mainnet01/chain/13/pact"
    }
    return APIHOST
}

const shorten_account = function (x) {
    // if (x == undefined) return "undefined"
    if (x.length > 2) {
        if (x.substr(0, 2) == "k:") {
            return x.substr(0, 6) + "..." + x.substr(x.length - 2, 2)
        } else if (x.length > 10) {
            return x.substr(0, 6) + "..." + x.substr(x.length - 2, 2)
        }
    }
    return x
}

const get_nonce = function () {
    return new Date().toUTCString()
}


