class NFTManager {
    appli;
    constructor(appli) {
        this.appli = appli
    }

    list_nfts_from(seeker) {
        var qthis = this
        return new Promise(function (resolve) {

            get_account(qthis.appli).then(acc => {
                var mcmd = {
                    type: "exec",
                    pactCode: "(free.coal.load-from " + seeker + ")",
                    nonce: get_nonce(),
                    envData: {},
                    meta: mymeta(acc.name),
                    networkId: get_cur_net()

                }

                return get_wallet_manager(qthis.appli).then(x => x.sendlocal(mcmd)).then((x) => {
                    resolve(qthis.make_nft_items(x.result.data).sort(id_sort()))
                })
            })
        })
    }

    mint_coal() {
        var qthis = this

        return get_account(qthis.appli).then((acc) => {
            var signingCmd = {
                "pactCode": "(free.coal.mint-coal)",
                "caps": [
                    {
                        "cap": {
                            "name": "coin.TRANSFER",
                            "args": [acc.name, qthis.appli.$store.state.COAL_MAKER, 13.69]
                        }
                    },
                    // {
                    //     "cap": {
                    //         "name": "free.coal.COALCAP",
                    //         "args": []
                    //     }
                    // },
                    {
                        "cap": {
                            "name": "coin.GAS",
                            "args": []
                        }
                    }
                ],
                "sender": acc.name,
                "ttl": 600,
                "gasLimit": 100000,
                "chainId": "13",
                "gasPrice": 0.000001,
                "envData": {
                    "coal-ks": [acc.public_key],
                    //   "account": localStorage.account
                },
                "signingPubKey": acc.public_key,
                "networkId": get_cur_net()
            }
            return get_wallet_manager(qthis.appli).then((x) => x.sign(signingCmd).then(y => x.send(y)))

        })

    }


    all_owned_by_wallet() {
        var qthis = this
        return get_account(qthis.appli).then((x) => qthis.all_owned_by(x.name))
    }

    make_nft_items(y) {
        var qthis = this
        if (y == undefined) {
            return []
        }
        return y.map((x) => new NFTItem(qthis.appli, x))
    }

    load_on_sale() {
        var qthis = this
        return new Promise(function (resolve, reject) {
            get_account(qthis.appli).then(acc => {

                Pact.fetch.local({
                    type: "exec",
                    // pactCode: "(free.coal.load-on-sale " + this.seeker + ")",
                    pactCode: "(free.coal.load-on-sale)",
                    nonce: get_nonce(),
                    envData: {},
                    meta: mymeta(acc.name),
                    networkId: get_cur_net()

                }, get_api_host()).then((x) => {
                    if (x.result.status == "success") {
                        resolve(qthis.make_nft_items(x.result.data).sort(id_sort()))

                    }
                    else {
                        reject({ reason: "fetch_fail" })
                    }
                })
            })
        })
    }

    all_owned_by(acc) {
        var qthis = this
        // return new Promise(function (resolve, reject) {
        return new Promise(function (resolve, reject) {

            Pact.fetch.local({
                type: "exec",
                pactCode: "(free.coal.all-owned-by \"" + acc + "\")",
                nonce: get_nonce(),
                envData: {},
                meta: mymeta(acc),
                networkId: get_cur_net()

            }, get_api_host()).then(function (x) {
                try {

                    if (x.result.status == "success") {

                        resolve(qthis.make_nft_items(x.result.data).sort(id_sort()))

                    }
                } catch (error) {
                    reject(error)
                }
            })
        })

        // })
    }
}

var id_sort = function () {
    return function (a, b) { return a.get_id() - b.get_id() }
}
var vget = function (x, k) {
    if (typeof x[k] == 'object') {
        if ('value' in x[k]) {

            return x[k].value
        }
    } else {
        return x[k]
    }
}
var vget2 = function (x) {
    if (typeof x == 'object') {
        if ('value' in x) {

            return x.value
        }
    } else {
        return x
    }
}
var vsetter = function (x, ks, v) {
    if (Array.isArray(ks)) {
        if (ks.length > 1) {
            vsetter(x[ks[0]], ks.splice(1), v)
        } else {
            if (typeof x[ks[0]] === 'object') {
                if ('value' in x[ks[0]]) {
                    x[ks[0]].value = v
                    return
                }
            }

            x[ks[0]] = v
        }
    } else {
        if (typeof x[ks] === 'object') {
            if ('value' in x[ks]) {
                x[ks].value = v
                return
            }
        }

        x[ks] = v
    }
}

