class XWallet extends WalletManager {
    myNetworkId;


    constructor(appli) {
        super(appli)


        // this.connection_state.value = ENUMS.WALLET.NOT_AVAILABLE
        var qthis = this


        window.kadena.on('res_checkStatus', (x) => {
            console.log("res_checkstatus", x)
            if (x.result.status == "fail") {
                vsetter(qthis, "connection_state", ENUMS.WALLET.DISCONNECTED)
            } else {
                vsetter(qthis, "chain_id", x.result.account.chainId)
                vsetter(qthis, "account_name", x.result.account.account)
                vsetter(qthis, "public_key", x.result.account.publicKey)
                vsetter(qthis, "connection_state", ENUMS.WALLET.CONNECED)
            }
        })
        window.kadena.on('res_accountChange', (event) => {
            console.log("accchng", event)
            qthis.appli.$store.state.last_view_focus = this.appli.$store.state.current_view
            qthis.appli.$store.state.current_view = ENUMS.VIEWS.MAIN

            qthis.connect()

        })
        window.kadena.request({
            method: 'kda_checkStatus',
            networkId: get_cur_net()
        }).then((x) => {
            console.log("statuscheck", x)
            if (x.status == "success") {
                console.log("WAL", x)

                vsetter(qthis, "chain_id", x.account.chainId)
                vsetter(qthis, "account_name", x.account.account)
                vsetter(qthis, "public_key", x.account.publicKey)
                vsetter(qthis, "connection_state", ENUMS.WALLET.CONNECED)

            } else {
                qthis.connect()

            }

        })

    }
    send(x) {
        var qthis = this
        return fetch(get_api_host() + "/api/v1/send", {
            'method': 'POST',
            'headers': {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            "body": JSON.stringify({ 'cmds': [x] })
        }).then(x => x.text()).then(x => mypolling(qthis.appli, x)).catch(x => {
            return new Promise(function (resolve, reject) {

                if (x.indexOf("row not found")) {
                    reject({ tmp: { result: { status: "failure" } } })
                } else {
                    resolve({ tmp: { result: { status: "success" ,} } })

                }
            })
        })
    }



    sign(signingCmd) {
        return new Promise(function (resolve, reject) {

            window.kadena.request({
                method: 'kda_requestSign',
                data: {
                    networkId: get_cur_net(),
                    signingCmd: signingCmd
                },
            }).then(x => {
                if (x.status == "success") {

                    resolve(x.signedCmd)
                } else {
                    reject()
                }
            })
        })
    }
    disconnect() {
        var qthis = this
        return new Promise(function (resolve, reject) {

            window.kadena.request({
                method: 'kda_disconnect',
                networkId: get_cur_net(),
            }).then((x) => {
                console.log("DC", x)
                if (x.result.status == "success") {
                    vsetter(qthis, "chain_id", undefined)
                    vsetter(qthis, "account_name", undefined)
                    vsetter(qthis, "public_key", undefined)
                    vsetter(qthis, "connection_state", ENUMS.WALLET.DISCONNECTED)
                    resolve()
                    // a
                } else {
                    reject()
                    // TODO 
                }
            })
        })
    }
    connect() {
        var qthis = this
        return new Promise(function (resolve, reject) {

            window.kadena.request({
                method: 'kda_connect',
                networkId: get_cur_net(),
            }).then((x) => {
                console.log("con", x)

                if (x.status == "success") {
                    vsetter(qthis, "chain_id", x.account.chainId)
                    vsetter(qthis, "account_name", x.account.account)
                    vsetter(qthis, "public_key", x.account.publicKey)
                    vsetter(qthis, "connection_state", ENUMS.WALLET.CONNECED)
                    resolve()
                } else {
                    // TODO 
                    reject()
                }
            })
        })

    }
}

